import baseURL from '../../config/index.js'
export default {
	onLoad(options) {
		let newQrcode = options.qrcode.slice(1,options.qrcode.length)
		
		this.qrcode = baseURL + newQrcode

		this.avatar = options.avatar
		if (options.avatar === "") {
			this.avatar = '../../static/img/order/avatar.png'
		}
		
		this.userName = options.userName
		this.inviteCode = options.inviteCode
		this.sharUrl = 'http://yonghu.gglaisong.com/?inviteCode='+this.inviteCode
		this.initRichText()
		this.invitedStatics()
	},
	data () {
		return {
			qrcode: '',
			avatar: '',
			userName: '',
			RichText: [],
			inviteCode: '',
			sharUrl:'',
			subNum: 0, // 推荐人数
			subAmount: 0
		}
	},
	methods: {
		goToBack () {
			uni.navigateBack({
				delta:1
			})
		},
		// 获取推荐人数
		async invitedStatics () {
			let res = await this.$fetch(this.$api.invitedStatics, {}, 'POST', 'FORM')
			this.subAmount = res.data.amount
			this.subNum = res.data.subNum
		},
		//分享微信
		shareWX() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 0,
			    href: this.sharUrl,
			    title: "GG来送",
			    summary: "我正在使用GG来送，赶紧跟我一起来体验！",
			    imageUrl: "../../static/96.png",
			    success: function (res) {
			        
					uni.showToast({
						icon:'success',
						title: '分享成功'
					})
			    },
			    fail: function (err) {
			        
					uni.showToast({
						icon:'success',
						title: '取消分享'
					})
			    }
			});
		},
		//分享朋友圈
		shareFriends () {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 0,
			    href: this.sharUrl,
			    title: "GG来送",
			    summary: "我正在使用GG来送，赶紧跟我一起来体验！",
			    imageUrl: "../../static/96.png",
			    success: function (res) {
			        
			    	uni.showToast({
			    		icon:'success',
			    		title: '分享成功'
			    	})
			    },
			    fail: function (err) {
			       
			    	uni.showToast({
			    		icon:'success',
			    		title: '取消分享'
			    	})
			    }
			});
		},
		//保存图片
		saveImage(){
			uni.downloadFile({
			    url: this.qrcode, //仅为示例，并非真实的资源
			    success: (res) => {
			        if (res.statusCode === 200) {
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									icon: "success",
									title: "保存成功"
									
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						});
			  
			        }
			    }
			});
		},
		async initRichText () {
			let res = await this.$fetch(this.$api.articleType,{type:6},"Post","Form")
			console.log(res)
			if (res.code == 0) {
				this.RichText = res.data
			}
		},
		goToRichText (id) {
			uni.navigateTo({
				url: '../rich-text/rich-text?id=' + id
			})
		}
	}
}
